
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import {formatTime} from '../maps/date.js'
import BScroll from 'better-scroll'
export default {
props:{
'detailInfo':{
type : Object,
required : true
},
'status':{
type : Boolean,
required : true
},
'assignedList':{
type : Array,
required : true
}
},
data(){
return{
acceptStandard : false,
storyshow : true,
technicalAnalysis : false,
approvalOpinion : false,
flowShow:false,
user:'',
distributionScroll: null
}
},
directives: {
TransferDom
},
components: {
Group,
Cell,
Datetime,
CellBox,
Popup

},
methods:{
formatTime,
change(value){

this.$emit('date',value)

},
changeUser(){
console.log("111");
this.flowShow = !this.flowShow;
},
getUser(item){
this.flowShow = false;
this.$emit("userId",item)
this.user = item.nickname;
}
},
watch:{
flowShow(val){
if(val){
this.$nextTick(() => {
if(!this.distributionScroll) {
this.distributionScroll = new BScroll(this.$refs.distribution, {click: true})
}
})
}
}
}



}



